

/* TABLET */
@media only screen and (min-width: 751px){
	.first{
		margin: 1% auto;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-evenly;
		width: 98%;
		height: auto;
	}
	.boxlink{
		text-decoration: none;
		border-radius: 5px;
		width: 23%;
		margin: 2% 0;
	}
	.box{
		border:solid 1px rgb(201, 200, 200);
		background-color: white;
		border-radius: 5px;
		text-align: center;
		height: 100%;
	}
	.boxImg img{
		width: 100%;
		height: 100%;
	}
	.boxImg{
		width:100%;
		height: 83%;
		margin: 0 auto;
		padding: 0;
	}
	
	.box h5{
		padding: 0;
		margin-top: 5%;
		margin-bottom: 0;
		color: black;
		font-weight: 500;
		font-size: 1.05em;
	}
	.box:hover h5{
		color: crimson;
		font-weight: 600;
	}
	.partTwo{
		background-color: white;
	}
	
	
	.main h2{
		background-color: crimson;
		padding: 1% 0;
		padding-left: 12.5%;
		color: white;
		margin: 0;
	}
	
	.background{
		background-color: white;
		width: 100%;
	}
	.loading{
		color:crimson;
		text-align: center;
		padding-bottom: 15%;
		margin: 7% auto;
		margin-bottom: 0;
		font-weight: 500;
	}
	.noprod{
		color: black;
		text-align: center;
		padding-bottom: 15%;
		margin: 7% auto;
		margin-bottom: 0;
		font-weight: 500;
	}
}
