

/* TABLET */
@media only screen and (min-width: 751px){
	.Test{
		display: grid;
		grid-template-columns: repeat(5, 1fr); 
		grid-auto-rows: auto; 
		grid-gap: 2%; 
		width: 90%;
		margin: 0 auto;
		margin-top: 2%;
		padding-bottom: 2%;
	}
	.Test a{
		background-color: rgb(230, 228, 228);
		text-align: center;
		border-radius: 5px;
		text-decoration: none;
		border: 1px solid transparent;
		height: 90%;
	}
	.Test h5{
		/* padding:0.5% 1%; */
		color:black;
		font-weight: 500;
	}
	.Test a:hover{
		background-color: rgb(223, 164, 176);
		border: 1px solid crimson;
		transition-duration: 0.3s;
	}
}
