

/* TABLET */
@media only screen and (min-width: 751px){
	.first{
		display: flex;
		justify-content: space-evenly;
		margin: 1% auto;
		width: 95%;
	}
	.boxlink{
		text-decoration: none;
		border-radius: 5px;
		width: 25%;
		border-left: 0.5px solid rgb(185, 185, 185);
		border-right: 0.5px solid rgb(185, 185, 185);
	}
	.box{
		background-color: white;
		text-align: center;
		padding: 1%;
		height: 100%;
	}
	.boxImg img{
		width: 100%;
		height: 100%;
	}
	.boxImg{
		width:80%;
		height: 70%;
		margin: 0 auto;
		margin-top: 4%;
		padding: 0;
	}
	
	.box h5{
		color: black;
		font-weight: 500;
		font-size: 1.05em;
	}
	.partTwo{
		background-color: white;
	}
	
	
	.main h2{
		background-color: crimson;
		padding: 1% 0;
		padding-left: 12.5%;
		color: white;
		margin: 0;
	}
	.background{
		background-color: white;
		width: 100%;
	}
	.loading{
		color:crimson;
		text-align: center;
		padding-bottom: 15%;
		margin: 7% auto;
		margin-bottom: 0;
		font-weight: 500;
	}
	.noprod{
		color: black;
		text-align: center;
		padding-bottom: 15%;
		margin: 7% auto;
		margin-bottom: 0;
		font-weight: 500;
	}
	.box:hover h5{
		color: crimson;
		font-weight: 600;
	}
	}
